html, body {
  overflow: hidden;
  height: 100%;
  margin: 0;
  padding: 0;
}

.app-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background: linear-gradient(to bottom, #219ebc, #8ecae6);
  padding: 24px;
  color: white;
  width: 100%;
  overflow-y: auto;
}

.title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 8px;
  width: 100%;
  max-width: 1100px;
}

.title-line {
  width: 100%;
  max-width: 1100px;
  border: 1px solid white;
  margin-bottom: 20px;
}

.grid-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  width: 100%;
  max-width: 1100px;
  box-sizing: border-box;
}

.box {
  flex: 0 1 500px;
  background-color: #023047;
  padding: 24px;
  border-radius: 12px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: left;
  border: 1px solid #3b82f6;
  box-sizing: border-box;
}

.file-input {
  margin-bottom: 16px;
  padding: 8px;
  border-radius: 8px;
  background-color: white;
  color: black;
}

.upload-button {
  background-color: #3b82f6;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  transition: background 0.3s;
  margin: 5px;
}

.upload-button:hover {
  background-color: #2563eb;
}

.result-text {
  font-size: 1rem;
}

.bold {
  font-weight: bold;
}

.error-text {
  color: #f87171;
}

.placeholder-text {
  color: #d1d5db;
}

.preview-container {
  margin-top: 16px;
}

.preview-image {
  width: 250px;
  height: 250px;
  object-fit: cover;
  border-radius: 8px;
  border: 2px solid #3b82f6;
}
